.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  display: flex;
  flex-direction: column;
  margin-top: 3%;
  align-items: center;
  padding: 25px;
}

.input {
  padding: 10px;
  margin: auto;
  border-radius: 10px;
  border: 1px solid black;
  margin-bottom: 15%;
}

.input:focus {
  outline: none;
}

.city {
  font-size: 25px;
  font-weight: 500;
}

.temp {
  font-size: 30px;
  font-weight: 600;
}

.temp-diff {
  font-size: 15px;
  font-weight: 500;
}

.temp-info {
  display: flex;
  justify-content: space-between;
}
